import React from "react";

const CommentsList = ({comments}) => (
  <>
    <h3>Comments:</h3>
    {comments.map((comment, key) => (
      <blockquote key={key}>
        <b>{comment.username}: </b>
        <cite>{comment.text}</cite>
      </blockquote>
    ))}
  </>
);

export default CommentsList;
